[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

/**
 * 1. Correct the inability to style clickable types in iOS and Safari.
 * 2. Change font properties to `inherit` in Safari.
 */
::-webkit-file-upload-button {
  /* 1 */
  -webkit-appearance: button;
  /* 2 */
  font: inherit; }

/**
 * Add the correct display in IE 10+.
 */
template {
  display: none; }

[hidden] {
  display: none; }

html {
  font-size: 16px; }
  @media screen and (min-width: 768px) {
    html {
      font-size: 18px; } }
  @media screen and (min-width: 1280px) {
    html {
      font-size: 20px; } }

body {
  font-family: -apple-system, system-ui, Segoe UI, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
  background: #fafbfc;
  text-rendering: optimizeLegibility; }

.closest-colors {
  display: grid;
  gap: 1rem;
  grid-template-columns: 100%; }
  @media screen and (min-width: 560px) and (max-width: 767px) {
    .closest-colors {
      grid-template-columns: repeat(2, calc(50% - 0.5rem)); } }
  @media screen and (min-width: 768px) and (max-width: 896px) {
    .closest-colors {
      grid-template-columns: repeat(3, calc(33.33333333% - 0.66666667rem)); } }
  @media screen and (min-width: 896px) and (max-width: 1024px) {
    .closest-colors {
      grid-template-columns: repeat(4, calc(25% - 0.75rem)); } }
  @media screen and (min-width: 1025px) {
    .closest-colors {
      grid-template-columns: repeat(5, calc(20% - 0.8rem)); } }

.color-display {
  position: relative;
  border-radius: 50%;
  width: 4em;
  box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12); }
  .color-display::before {
    content: '';
    width: 1px;
    margin-left: -1px;
    float: left;
    height: 0;
    padding-top: 100%; }
  .color-display::after {
    content: '';
    display: table;
    clear: both; }

.page,
[class^='page--'],
[class*=' page--'] {
  max-width: 1024px;
  margin: 2em; }

.page-section + .page-section {
  margin-top: 1em; }

.selectable-text__input {
  display: block;
  border: none;
  width: 100%;
  height: auto;
  background: transparent;
  color: inherit;
  line-height: inherit;
  font-family: inherit;
  outline: none;
  padding: 0; }

.selector__select {
  display: block; }

.site-menu {
  margin: 2em; }
  .site-menu__list {
    display: flex; }
  .site-menu__list-item {
    flex: 0 0 auto;
    margin: 0 0 0 1em; }
    .site-menu__list-item:first-child {
      margin-left: 0; }
  .site-menu__link {
    display: block;
    line-height: 1.5;
    text-decoration: none; }
    .site-menu__link.is-exact-active {
      font-weight: bold; }

.textfield__label {
  display: block;
  line-height: 1.5;
  margin-bottom: .25em; }

.textfield__input, .textfield__textarea {
  display: block;
  width: 100%;
  padding: 0.4em 0.6em;
  line-height: 1.5;
  background: white;
  border: 2px solid #dcddde;
  border-radius: 4px;
  outline: none; }

.textfield__textarea {
  min-width: 100%;
  max-width: 100%;
  height: calc(5.3em + 4px);
  min-height: calc(2.3em + 4px);
  max-height: calc(12.8em + 4px); }
